* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 25px;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.buttons-container {
  text-align: center;
  align-items: center;
}

button {
  width: 100px;
  height: 50px;
  background-color: rgba(250, 250, 250, .514);
  border-radius: 15px;
}

button:hover {
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
}

.currentcolor-name {
  text-align: center;
  background-color: rgba(250, 250, 250, .604);
  border: 2px solid #000;
  border-radius: 10px;
  align-items: center;
  margin-top: 20px;
}

span {
  margin-left: 10px;
  font-family: monospace;
  font-size: 30px;
}

